      <!---->
      <el-dialog title="" :visible.sync="other" :close-on-click-modal="false">
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="" name="first">
            <!-- -->
            <el-table
              :data="items"
              style="width: 100%">
              <el-table-column type="expand">
                <template slot-scope="props">
                  <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item label="">
                      <span>{{ props.row.score }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{ props.row.gold }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{ props.row.cost }}</span>()
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{Math.round(props.row.onlineTime/(1000*60*60)) }}</span>()<!---->
                    </el-form-item>
                    <el-form-item label="IP">
                      <span>{{ props.row.loginIp }}</span>
                    </el-form-item>
                  </el-form>
                </template>
              </el-table-column>
