          name: ''
        },
        total: 0,
        page: 0,
        limit: 10,
        loading: false,
        addLoading: false,
        addFormRules: {
          nickname: [
            {required: true, message: '', trigger: 'blur'}
          ],
          pass: [
            {required: true, message: '', trigger: 'blur'}
          ],
          user: [
