        }
      }
    },
    methods: {
      trim(str) {
        if(str != null){
          return str.replace(/(^\s+)|(\s+$)/g, "");
        }
      },
      handleClick(tab, event) {
        if (tab.index == "0") {
          axios({
            method: 'post',
            url: '/api/member/querymember',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'id': sessionStorage.getItem('id')
            }
          })
            .then((res) => {
                this.loading = false;//
                this.items = res.data.data.items;
                //console.log(res.data.data.items)
              },
            ).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 500:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
              }
            }
          });
        }
        if (tab.index == "1") {
          this.goldwathercourse(1);//
        }
      },
      //
      showother: function (index, row) {
        sessionStorage.setItem('id', this.vip[index].id);//id
        //console.log(this.vip[index].id)
        axios({
          method: 'post',
          url: '/api/member/querymember',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'id': sessionStorage.getItem('id')
          }
        })
          .then((res) => {
              this.loading = false;//
              this.items = res.data.data.items;
              this.page = res.data.data.pageCount;
              // console.log(res.data.data.items)
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
        this.other = true;
        this.addForm = {
          name: '',
          author: '',
          publishAt: '',
          description: ''
        };
      },

      //
      givegold() {
        let ids = this.sels.map(item => item.id).toString();
        let that = this;
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          if (this.normalForm.gold == undefined || this.normalForm.gold == "") {
            this.$message({
              showClose: true,
              message: '',
              type: 'warning'
            });
          } else if (this.normalForm.gold < 0) {
            this.$message({
              showClose: true,
              message: '',
              type: 'warning'
            });
          } else {
            that.loading = true;
            axios({
              method: 'post',
              url: '/api/member/give_reward',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params: {
                'gold': this.trim(this.normalForm.gold),
                'id': ids
              }
            })
              .then((res) => {
                  that.loading = false;
                  if (res.data.success == true) {
                    that.$message.success({showClose: true, message: '', duration: 1500});
                    this.normalForm.gold = '';//
                    this.giveFormVisible = false;//
                  } else {
                    that.$message.error({showClose: true, message: err.toString(), duration: 2000});
                  }
                },
              ).catch((e) => {
              if (e && e.response) {
                switch (e.response.status) {
                  case 504:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break;
                  case 500:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break;
                  case 405:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    break;
                  case 400:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    that.loading = false;
                    break;
                }
              }
            });
          }
        });
      },
      //
      showgold: function (index, row) {
        this.giveFormVisible = true;
        this.addForm = {
          name: '',
          author: '',
          publishAt: '',
          description: ''
        };
      },

      //
      giveintegral() {
        let ids = this.sels.map(item => item.id).toString();
        let that = this;
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          if (this.normalForm.score == undefined || this.normalForm.score == "") {
            this.$message({
              showClose: true,
              message: '',
              type: 'warning'
            });
          } else if (this.normalForm.score < 0) {
            this.$message({
              showClose: true,
              message: '',
              type: 'warning'
            });
          } else {
            that.loading = true;
            axios({
              method: 'post',
              url: '/api/member/give_reward',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params: {
                'score': this.trim(this.normalForm.score),
                'id': ids
              }
            })
              .then((res) => {
                  that.loading = false;
                  if (res.data.success == true) {
                    that.$message.success({showClose: true, message: '', duration: 1500});
                    this.normalForm.score = '';//
                    this.givefromintegral = false;//
                  } else {
                    that.$message.error({showClose: true, message: err.toString(), duration: 2000});
                  }
                },
              ).catch((e) => {
              if (e && e.response) {
                switch (e.response.status) {
                  case 504:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break;
                  case 500:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break;
                  case 405:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    break;
                  case 400:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    break;
                }
              }
            });
          }
        });
      },
      //
      showintegral: function (index, row) {
        this.givefromintegral = true;
        this.addForm = {
          name: '',
          author: '',
          publishAt: '',
          description: ''
        };
      },

      change() {
        if (this.value == '1') {//
          this.loading = true;//
          axios({//
            method: 'post',
            url: '/api/member/querymember',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'size': '15',//
              'page': this.page,//
              'id': this.trim(this.filters.id),
              'nickname': this.trim(this.filters.nickname),
              'vip': true
            }
          })
            .then((res) => {
                this.loading = false;//
                this.vip = res.data.data.items;
                this.total = res.data.data.pageCount;//
                for (let i = 0; i < this.vip.length; i++) {
                  this.vip[i].vipEndTime = this.dateTimeFormat(this.vip[i].vipEndTime);
                  this.vip[i].createTime = this.dateTimeFormat(this.vip[i].createTime);
                }
              },
            ).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 500:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
              }
            }
          });
        } else if (this.value == '2') {//
          this.loading = true;//
          axios({//
            method: 'post',
            url: '/api/member/querymember',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'size': '15',//
              'page': this.page,//
              'id': this.trim(this.filters.id),
              'nickname': this.trim(this.filters.nickname),
              'vip': false
            }
          })
            .then((res) => {
                this.loading = false;//
                this.vip = res.data.data.items;
                this.total = res.data.data.pageCount;//
                for (let i = 0; i < this.vip.length; i++) {
                  this.vip[i].vipEndTime = this.dateTimeFormat(this.vip[i].vipEndTime);
                  this.vip[i].createTime = this.dateTimeFormat(this.vip[i].createTime);
                }
              },
            ).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 500:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
              }
            }
          });
        }
      },

      ChangePage(val) {
        this.page = val;
        this.gold(this.page);
      },

      //
      goldwathercourse() {
        axios({
          method: 'post',
          url: '/api/member/querygoldrecord',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'size': '15',//
            'page': this.page,//
            'memberId': sessionStorage.getItem('id')  //0023
          }
        })
          .then((res) => {
              this.loading = false;//
              this.items = res.data.data.items;
              this.page = res.data.data.pageNum;//
              for (let i = 0; i < this.items.length; i++) {
                this.items[i].accountingTime = this.dateTimeFormat(this.items[i].accountingTime);
              }
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      },

      //
      integral() {
        axios({
          method: 'post',
          url: '/api/member/queryscorerecord',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'size': '15',//
            'page': this.page,//
            'memberId': sessionStorage.getItem('id')//0023
          }
        })
          .then((res) => {
              this.loading = false;//
              this.items = res.data.data.items;
              this.page = res.data.data.pageNum;//
              for (let i = 0; i < this.items.length; i++) {
                this.items[i].accountingTime = this.dateTimeFormat(this.items[i].accountingTime);
              }
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      },

      handleCurrentChange(val) {
        this.page = val;
        this.handleSearch(this.page);
      },
      dateTimeFormat(value) {
        let time = new Date(+value);
        let rightTwo = (v) => {
          v = '0' + v;
          return v.substring(v.length - 2, v.length)
        };
        if (time == null) return;
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let date = time.getDate();
        let hours = time.getHours();
        let minutes = time.getMinutes();
        let seconds = time.getSeconds();
        return year + '-' + rightTwo(month) + '-' + rightTwo(date) + ' ' + rightTwo(hours) + ':' + rightTwo(minutes) + ':' + rightTwo(seconds);
      },
      selsChange: function (sels) {
        this.sels = sels;
      },
    },
    mounted() { //
      this.loading = true;//
      axios({//
        method: 'post',
        url: '/api/member/querymember',
        headers: {
          'Content-type': 'application/x-www-form-urlencoded'
        },
        params: {
          'size': '15',//
          'page': this.page,//
        }
      })
        .then((res) => {
            this.loading = false;//
            this.vip = res.data.data.items;
            this.total = res.data.data.pageCount;//
            for (let i = 0; i < this.vip.length; i++) {
              this.vip[i].vipEndTime = this.dateTimeFormat(this.vip[i].vipEndTime);
              this.vip[i].createTime = this.dateTimeFormat(this.vip[i].createTime);
            }
          },
        ).catch((e) => {
        if (e && e.response) {
          switch (e.response.status) {
            case 504:
              this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              });
              this.loading = false;//
              break
            case 500:
              this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              });
              this.loading = false;//
              break
            case 405:
              this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              });
              break
          }
        }
      });
    }
