import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login.vue'
import Home from '@/components/Home.vue'
import Admin from '@/components/Admin.vue'
import NewVip from '@/components/NewVip.vue'
import MemberRules from '@/components/MemberRules.vue'
import SystemMaintenance from '@/components/SystemMaintenance.vue'
import SystemRecovery from '@/components/SystemRecovery.vue'
import Role from '@/components/Role.vue'
import Jurisdiction from '@/components/Jurisdiction.vue'
import Server from '@/components/Server.vue'
import SystemNotice from '@/components/SystemNotice.vue'
import MemberCard from '@/components/MemberCard.vue'
import Microservice from '@/components/Microservice.vue'
import MemberLevel from '@/components/MemberLevel.vue'
import ReCharge from '@/components/ReCharge.vue'
import OnlineUsers from '@/components/OnlineUsers.vue'
import PlatformOperation from '@/components/PlatformOperation.vue'
import Progression from '@/components/Progression.vue'
import PendingReleaseTask from '@/components/PendingReleaseTask.vue'
import PublishedTask from '@/components/PublishedTask.vue'
import MailingList from '@/components/MailingList.vue'
import Mailing from '@/components/Mailing.vue'
import MailRecord from '@/components/MailRecord.vue'
import PromoterManagement from '@/components/PromoterManagement.vue'
import PromoterApplicationRecord from '@/components/PromoterApplicationRecord.vue'
import MembershipCardPurchaseRecord from '@/components/MembershipCardPurchaseRecord.vue'
import MembershipCardConsumptionRecord from '@/components/MembershipCardConsumptionRecord.vue'
import PromoterOperationRecord from '@/components/PromoterOperationRecord.vue'
import Activity from '@/components/Activity.vue'
import MallManagement from '@/components/MallManagement.vue'
import CodeUsageRecord from '@/components/CodeUsageRecord.vue'
import IntegralRecord from '@/components/IntegralRecord.vue'
import GamePlay from '@/components/GamePlay.vue'
import PlayTheMutex from '@/components/PlayTheMutex.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      component: Home,
      redirect: '/admin',
      menuShow: true,
      leaf: true, // 
      iconCls: 'iconfont icon-guanliyuan', // class
      children: [
        {path: '/admin', component: Admin, name: '', menuShow: true}
      ]
    },
    {
      path: '/home',
      component: Home,
      redirect: '/role',
      menuShow: true,
      leaf: true, // 
      iconCls: 'iconfont icon-navicon-jsgl', // class
      children: [
        {path: '/role', component: Role, name: '', menuShow: true}
      ]
    },
    {
      path: '/home',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-huiyuanzhongxin',
      children: [
        {path: '/newVip', component: NewVip, name: '', menuShow: true},
        {path: '/memberRules', component: MemberRules, name: '', menuShow: true},
        {path: '/memberCard', component: MemberCard, name: '', menuShow: true},
        {path: '/memberLevel', component: MemberLevel, name: 'vip', menuShow: true},
        {path: '/reCharge', component: ReCharge, name: '', menuShow: true},
        {path: '/onlineUsers', component: OnlineUsers, name: '', menuShow: true},
      ]
    },
    {
      path: '/home',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-xitongshezhi',
      children: [
        {path: '/systemMaintenance', component: SystemMaintenance, name: '', menuShow: true},
        {path: '/systemRecovery', component: SystemRecovery, name: '', menuShow: true},
        {path: '/systemNotice', component: SystemNotice, name: '', menuShow: true},
        {path: '/Microservice', component: Microservice, name: '', menuShow: true}
      ]
    },
    {
      path: '/home',
      component: Home,
      redirect: '/jurisdiction',
      menuShow: true,
      leaf: true, // 
      iconCls: 'iconfont icon-ic_opt_feature', // class
      children: [
        {path: '/jurisdiction', component: Jurisdiction, name: '', menuShow: true}
      ]
    },
    {
      path: '/home',
      component: Home,
      redirect: '/server',
      menuShow: true,
      leaf: true, // 
      iconCls: 'iconfont icon-server', // class
      children: [
        {path: '/server', component: Server, name: '', menuShow: true}
      ]
    },
    {
      path: '/home',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-baobiaotongji',
      children: [
        {path: '/platformOperation', component: PlatformOperation, name: '', menuShow: true},
        {path: '/progression', component: Progression, name: '', menuShow: true},
      ]
    },
    {
      path: '/home',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-renwuguanli',
      children: [
        {path: '/pendingReleaseTask', component: PendingReleaseTask, name: '', menuShow: true},
        {path: '/publishedTask', component: PublishedTask, name: '', menuShow: true},
      ]
    },
    {
      path: '/home',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-mailmanagement',
      children: [
        {path: '/mailingList', component: MailingList, name: '', menuShow: true},
        {path: '/mailing', component: Mailing, name: '', menuShow: true},
        {path: '/mailRecord', component: MailRecord, name: '', menuShow: true},
      ]
    },
    {
      path: '/home',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-yewurenyuanxinxiguanli',
      children: [
        {path: '/promoterManagement', component: PromoterManagement, name: '', menuShow: true},
        {path: '/promoterApplicationRecord', component: PromoterApplicationRecord, name: '', menuShow: true},
        {
          path: '/membershipCardPurchaseRecord',
          component: MembershipCardPurchaseRecord,
          name: '',
          menuShow: true
        },
        {
          path: '/membershipCardConsumptionRecord',
          component: MembershipCardConsumptionRecord,
          name: '',
          menuShow: true
        },
        {path: '/integralRecord', component: IntegralRecord, name: '', menuShow: true},
        {path: '/promoterOperationRecord', component: PromoterOperationRecord, name: '', menuShow: true},
        {path: '/mallManagement', component: MallManagement, name: '', menuShow: true},
        {path: '/codeUsageRecord', component: CodeUsageRecord, name: '', menuShow: true}
      ]
    },
    {
      path: '/home',
      component: Home,
      menuShow: true,
      leaf: true,
      iconCls: 'iconfont icon-huodongguanli',
      children: [
        {path: '/activity', component: Activity, name: '', menuShow: true}
      ]
    },
    {
      path: '/home',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-wanfa',
      children: [
        {path: '/gamePlay', component: GamePlay, name: '', menuShow: true},
        {path: '/playTheMutex', component: PlayTheMutex, name: '', menuShow: true},
      ]
    },
  ]
})



