<!---->
<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.taskDocId" placeholder="ID"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="filters.promulgator" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-select v-model="filters.vip" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="filters.type" placeholder="">
              <el-option
                v-for="(item,index) in data"
                :key="index"
                :label="item.label"
                :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-button type="primary" @click="showtask"></el-button>
        </el-form>
      </el-col>
    </el-col>

    <!-- -->
    <el-table :data="items" highlight-current-row @selection-change="selsChange"
              style="width: 100%;">
      <el-table-column type="selection" width="50"></el-table-column>
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="taskDocId" label="ID" width="120" sortable></el-table-column>
      <el-table-column prop="promulgator" label="" width="120" sortable></el-table-column>
      <el-table-column prop="name" label="" width="100" sortable></el-table-column>
      <el-table-column prop="desc" label="" width="100" sortable></el-table-column>
      <el-table-column prop="type" label="" width="100" sortable></el-table-column>
      <el-table-column prop="rewardGold" label="" width="120" sortable></el-table-column>
      <el-table-column prop="rewardScore" label="" width="100" sortable></el-table-column>
      <el-table-column prop="rewardVip" label="" width="140" sortable></el-table-column>
      <el-table-column prop="targetNum" label="" width="100" sortable></el-table-column>

      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="danger" @click="deletetask(scope.$index,scope.row)" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <el-button type="danger" @click="deletechecktask" :disabled="this.sels.length===0"></el-button>
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total"
                     style="float:right;">
      </el-pagination>
    </el-col>

  </el-row>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "PublishedTask",
    data() {
      return {
        options: [{
          value: null,
          label: ''
        }, {
          value: true,
          label: ''
        }, {
          value: false,
          label: ''
        }],
        value: '',
        filters: {
          name: ''
        },
        loading: false,
        items: [],
        sels: [], //
        total: 0,
        data: []
      }
    },
    methods: {
      trim(str) {
        if(str != null){
          return str.replace(/(^\s+)|(\s+$)/g, "");
        }
      },
      selsChange: function (sels) {
        this.sels = sels;
      },
      handleCurrentChange(val) {
        this.page = val;
        this.showtask(this.page);
      },
      //
      showtask() {
        axios({
          method: 'post',
          url: '/api/task/querytaskdocumenthistory',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'size': '15',//
            'page': this.page,//
            'taskDocId': this.trim(this.filters.taskDocId),
            'promulgator': this.trim(this.filters.promulgator),
            'vip': this.value,
          }
        })
          .then((res) => {
              this.items = res.data.data.items;
              this.total = res.data.data.pageCount;
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      },

      //
      deletetask: function (index, row) {
        let that = this;
        this.$confirm('?', '', {type: 'warning'}).then(() => {
          that.loading = true;
          axios({
            method: 'post',
            url: '/api/task/withdraw',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'taskId': row.id
            }
          })
            .then((res) => {
                that.loading = false;
                if (res.data.success == true) {
                  that.$message.success({showClose: true, message: '', duration: 1500});
                  that.showtask();
                } else if (res.data.success == false) {
                  that.$message.error({showClose: true, message: err.toString(), duration: 2000});
                }
              },
            ).catch((e) => {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '', duration: 2000});
          });
        });
      },

      //
      deletechecktask: function () {
        let ids = this.sels.map(item => item.id).toString();
        let that = this;
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          that.loading = true;
          axios({
            method: 'post',
            url: '/api/task/withdraw',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'taskId': ids
            }
          })
            .then((res) => {
                that.loading = false;
                if (res.data.success == true) {
                  that.$message.success({showClose: true, message: '', duration: 1500});
                  this.showtask();
                } else if (res.data.success == false) {
                  that.$message.error({showClose: true, message: err.toString(), duration: 2000});
                }
              },
            ).catch((e) => {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '', duration: 2000});
          });
        });
      }
    },
    mounted() { //
      this.showtask();
      axios({//
        method: 'post',
        url: '/api/task/querytasktype',
        headers: {
          'Content-type': 'application/x-www-form-urlencoded'
        },
      })
        .then((res) => {
            this.data = res.data.data;
          },
        ).catch((e) => {
        if (e && e.response) {
          switch (e.response.status) {
            case 504:
              this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              });
              this.loading = false;//
              break
            case 500:
              this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              });
              this.loading = false;//
              break
            case 405:
              this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              });
              break
          }
        }
      });
    }
  }
</script>

<style scoped>
  .warp-main {
    margin-top: 20px;
  }

  .toolbar {
    margin-top: 20px;
  }
</style>



