<!---->
<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
          <el-form-item label="">
            <el-input v-model="filters.promulgator" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="handleSearch"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!-- -->
      <el-table :data="items" highlight-current-row @selection-change="selsChange"
                style="width: 100%;">
        <el-table-column prop="theme" label="" width="100" sortable></el-table-column>
        <el-table-column prop="content" label="" width="100" sortable>
          <template slot-scope="scope">
            <img :src="scope.row.content" alt="" style="width: 50px;height: 50px">
          </template>
        </el-table-column>
        <el-table-column prop="url" label="" width="180" sortable></el-table-column>
        <el-table-column prop="promulgator" label="" width="100" sortable></el-table-column>
        <el-table-column prop="remainSecond" label="">
          <template slot-scope="scope">
            <el-button type="primary" v-if="scope.row.state === 'START'" :disabled="false"
                       @click="off(scope.$index,scope.row)">
            </el-button>
            <el-button type="info" disabled v-if="scope.row.state === 'STOP'"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-col :span="24" class="toolbar">
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total"
                       style="float:right;">
        </el-pagination>
      </el-col>

      <!---->
      <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
        <el-form :model="normalForm" label-width="100px" :rules="rules" class="demo-ruleForm">
          <el-form-item label="" prop="title">
            <el-input v-model="normalForm.title" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="file">
            <div class="upload">
              <el-upload
                class="avatar-uploader"
                :action=domain
                :http-request=upqiniu
                :show-file-list="false"
                :before-upload="beforeUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <el-button v-else class="el-icon-plus avatar-uploader-icon"></el-button>
              </el-upload>
            </div>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model="normalForm.address" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="issue"></el-button>
            <el-button type="primary" @click.native="addFormVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

    </el-col>
  </el-row>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "Activity",

    data() {
      return {
        imageUrl: '',
        // 
        domain: 'http://up.qiniu.com',
        // 
        qiniuaddr: 'qiniu.3cscy.com',
        normalForm: {},
        rules: {
          title: [
            {required: true, message: '', trigger: 'blur'}
          ],
          file: [
            {required: true, message: '', trigger: 'blur'}
          ],
          address: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        items: [],
        filters: {
          name: ''
        },
        total: 0,
        loading: false,
        addFormVisible: false,//
      }
    },
    methods: {
      trim(str) {
        if(str != null){
          return str.replace(/(^\s+)|(\s+$)/g, "");
        }
      },
      // 
      upqiniu(req) {
        //console.log(req)
        const config = {
          headers: {'Content-Type': 'multipart/form-data'}
        }
        let filetype = ''
        if (req.file.type === 'image/png') {
          filetype = 'png'
        } else {
          filetype = 'jpg'
        }
        // 
        const keyname = 'anbang' + Math.random() + '.' + filetype
        // token
        axios({
          method: 'post',
          url: '/api/mailctrl/uptoken',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
        }).then(res => {
          const formdata = new FormData()
          formdata.append('file', req.file)
          formdata.append('token', res.data.data)
          formdata.append('key', keyname)
          // 
          axios.post(this.domain, formdata, config).then(res => {
            this.imageUrl = 'http://' + this.qiniuaddr + '/' + res.data.key
            //console.log(this.imageUrl)
          })
        })
      },
      // 
      beforeUpload(file) {
        const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
        const isLt2M = file.size / 1024 / 1024 < 2
        if (!isJPG) {
          this.$message.error(' JPG !')
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!')
        }
        return isJPG && isLt2M
      },

      //
      issue(req) {
        if (this.normalForm.title == undefined || this.normalForm.title == "") {
          this.$message({
             showClose: true,
            message: '',
            type: 'warning'
          });
        } else {
          axios({
            method: 'post',
            url: '/api/activity/addactivity',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'theme': this.trim(this.normalForm.title),
              'content': this.imageUrl,
              'url': this.trim(this.normalForm.address),
              'promulgator': sessionStorage.getItem('nickname')
            }
          })
            .then((res) => {
                if (res.data.success == false) {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                } else if (res.data.success == true) {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                  });
                  this.normalForm.title = ''
                  this.imageUrl = ''
                  this.normalForm.address = ''
                  this.addFormVisible = false;//
                  this.handleSearch();
                }
              },
            ).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 500:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
              }
            }
          });
        }
      },
      showAddDialog: function () {
        this.addFormVisible = true;
        this.addForm = {
          name: '',
          author: '',
          publishAt: '',
          description: ''
        };
      },
      handleCurrentChange(val) {
        this.page = val;
        this.handleSearch(this.page);
      },
      handleSearch() {
        axios({
          method: 'post',
          url: '/api/activity/queryactivity',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'size': '15',//
            'page': this.page,//
            'promulgator': this.trim(this.filters.promulgator),//
            'state': 'START'//
          }
        })
          .then((res) => {
              this.loading = false;//
              this.items = res.data.data.items;
              this.total = res.data.data.pageCount;//
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      },
      selsChange: function (sels) {
        this.sels = sels;
      },

      //
      off(index, row) {
        sessionStorage.setItem('id', this.items[index].id);//id
        axios({
          method: 'post',
          url: '/api/activity/stopactivity',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'activityId': sessionStorage.getItem('id')
          }
        })
          .then((res) => {
              this.handleSearch()
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      }
    },
    mounted() { //
      this.handleSearch()
    },
  }
</script>

<style scoped>
  .warp-main {
    margin-top: 20px;
  }

  img {
    width: 180px;
    height: 120px;
  }
</style>



