<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.adminname" placeholder="" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" v-on:click="handleSearch"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!-- -->
      <el-table :data="list" highlight-current-row @selection-change="selsChange"
                style="width: 100%;">
        <el-table-column type="index" width="60"></el-table-column>
        <el-table-column prop="adminname" label="" width="160" sortable></el-table-column>
        <el-table-column prop="notice" label="" width="300" sortable></el-table-column>
        <el-table-column prop="place" label="" width="160" sortable>
          <template slot-scope="scope">
            <el-button type="text" v-if="scope.row.place === '0'"></el-button>
            <el-button type="text" v-if="scope.row.place === '1'"></el-button>
            <el-button type="text" v-if="scope.row.place === '2'"></el-button>
          </template>
        </el-table-column>
        <el-table-column prop="state" label="">
          <template slot-scope="scope">
            <el-button type="primary" v-if="scope.row.state === 1" :disabled="false" @click="off(scope.$index,scope.row)"></el-button>
            <el-button type="info" disabled v-if="scope.row.state === 0"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-col :span="24" class="toolbar">
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total"
                       style="float:right;">
        </el-pagination>
      </el-col>

      <!---->
      <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
        <el-form :model="normalForm" label-width="100px" :rules="rules" class="demo-ruleForm">
          <el-form-item label="" prop="notice">
            <el-input
              type="textarea"
              autosize
              placeholder=""
              v-model="normalForm.notice">
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="place">
            <template>
              <el-radio v-model="place" label="0"></el-radio>
              <el-radio v-model="place" label="1"></el-radio>
              <el-radio v-model="place" label="2"></el-radio>
            </template>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="issue"></el-button>
            <el-button type="primary" @click.native="addFormVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "History",
    data() {
      return {
        place: '1',
        normalForm: {},
        rules: {
          notice: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        list: [],
        sels: [], //
        filters: {
          name: ''
        },
        total: 0,
        loading: false,
        addFormVisible: false,//
      }
    },
    methods: {
      trim(str) {
        if(str != null){
          return str.replace(/(^\s+)|(\s+$)/g, "");
        }
      },
      //
      off(index, row) {
        sessionStorage.setItem('id', this.list[index].id);//id
        axios({
          method: 'post',
          url: '/api/noticectrl/updatenotice',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'id': sessionStorage.getItem('id')
          }
        })
          .then((res) => {
            this.handleSearch()
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      },
      //
      issue() {
        if (this.normalForm.notice == undefined || this.normalForm.notice == "") {
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          });
        } else {
          axios({
            method: 'post',
            url: '/api/noticectrl/addnotice',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'status': '0',
              'notice': this.trim(this.normalForm.notice),
              'place': this.trim(this.place)
            }
          })
            .then((res) => {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                });
                this.normalForm.notice = ''
                this.addFormVisible = false;//
                this.handleSearch();
              },
            ).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 500:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
              }
            }
          });
        }
      },
      handleCurrentChange(val) {
        this.page = val;
        this.handleSearch(this.page);
      },
      handleSearch() {
        this.loading = true;//
        axios({//
          method: 'post',
          url: '/api/noticectrl/querynotice',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'size': '15',//
            'page': this.page,//
            'adminname': this.trim(this.filters.adminname)
          }
        })
          .then((res) => {
              this.loading = false;//
              this.list = res.data.list;
              this.total = res.data.count;//
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      },
      selsChange: function (sels) {
        this.sels = sels;
      },
      showAddDialog: function () {
        this.addFormVisible = true;
        this.addForm = {
          name: '',
          author: '',
          publishAt: '',
          description: ''
        };
      }
    },
    mounted() { //
      this.handleSearch()
    }
  }
</script>

<style scoped>
  .warp-main {
    margin-top: 20px;
  }
</style>



