<!---->
<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.id" placeholder="ID" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-select v-model="value" placeholder="" @change="change">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>

      <!-- -->
      <el-table :data="onlineUsers" highlight-current-row @selection-change="selsChange"
                style="width: 100%;">
        <el-table-column type="index" width="60"></el-table-column>
        <el-table-column prop="id" label="ID" width="100" sortable></el-table-column>
        <el-table-column prop="nickname" label="" width="100" sortable></el-table-column>
        <el-table-column prop="gender" label="" width="160" sortable></el-table-column>
        <el-table-column prop="phone" label="" width="100" sortable></el-table-column>
        <el-table-column prop="createTime" label="" width="100" sortable></el-table-column>
        <el-table-column prop="vipEndTime" label="" width="100" sortable></el-table-column>
        <el-table-column prop="vipEndTime" label="" width="100" sortable></el-table-column>
        <el-table-column prop="vipEndTime" label=""></el-table-column>
      </el-table>

      <!---->
      <el-col :span="24" class="toolbar">
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total"
                       style="float:right;">
        </el-pagination>
      </el-col>

    </el-col>
  </el-row>
</template>

<script>
  export default {
    name: "OnlineUsers",
    data() {
      return {
        onlineUsers: [],
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        },],
        value: '',
        filters: {
          name: ''
        },
        total: 0,
        loading: false,
      }
    },
    methods: {
      trim(str) {
        if(str != null){
          return str.replace(/(^\s+)|(\s+$)/g, "");
        }
      },
      change() {
        if (this.value == '1') {//

        } else if (this.value == '2') {//

        }
      },
      selsChange: function (sels) {
        this.sels = sels;
      },
      handleCurrentChange(val) {//
        this.page = val;
        this.handleSearch(this.page);
      },
    }
  }
</script>

<style scoped>
  .warp-main {
    margin-top: 20px;
  }
</style>



