<!---->
<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
          <el-form-item label="ID">
            <el-input v-model="filters.id" placeholder="ID"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="filters.agent" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="filters.startTime"
              type="date"
              placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="filters.endTime"
              type="date"
              placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-button type="primary" @click="seek"></el-button>
        </el-form>
      </el-col>

      <!-- -->
      <el-table :data="items" highlight-current-row @selection-change="selsChange"
                style="width: 100%;">
        <el-table-column type="index" width="60"></el-table-column>
        <el-table-column prop="agentId" label="ID" width="120" sortable></el-table-column>
        <el-table-column prop="agent" label="" width="120" sortable></el-table-column>
        <el-table-column prop="product" label="" width="100" sortable></el-table-column>
        <el-table-column prop="number" label="" width="100" sortable></el-table-column>
        <el-table-column prop="accountingAmount" label="" width="100" sortable></el-table-column>
        <el-table-column prop="accountingTime" label="" width="160" sortable></el-table-column>
        <el-table-column prop="summary.text" label="" width="140" sortable></el-table-column>
        <el-table-column prop="balanceAfter" label="" width="100" sortable></el-table-column>
        <el-table-column prop="remainSecond" label="">
          <template slot-scope="scope">
            <el-button type="primary" @click="publishDialog(scope.$index,scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-col :span="24" class="toolbar">
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total"
                       style="float:right;">
        </el-pagination>
      </el-col>

      <!---->
      <el-dialog title="" :visible.sync="publishVisible" :close-on-click-modal="false">
        <el-form :model="publishForm" label-width="220px">
          <el-form-item label="ID">
            <el-input class="memberInput" v-model="publishForm.agentId" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="publishForm.product" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input class="memberInput" v-model="publishForm.number" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input class="memberInput" v-model="publishForm.afternumber"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input class="memberInput" v-model="publishForm.accountingAmount" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input class="memberInput" v-model="publishForm.afterAmount"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="notarizeVisible = true,publishVisible =false"></el-button>
            <el-button type="primary" @click.native="publishVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="notarizeVisible" :close-on-click-modal="false">
        <el-form>
          <div align="center">
            {{publishForm.agentId}}<br/>{{publishForm.product}}{{publishForm.number}}{{publishForm.afternumber}}<br/>
            {{publishForm.accountingAmount}}{{publishForm.afterAmount}}
          </div>
          <br/>
          <div align="center">
            <el-button type="primary" @click="sure"></el-button>
            <el-button type="primary" @click.native="notarizeVisible = false,publishVisible = true"></el-button>
          </div>
        </el-form>
      </el-dialog>

    </el-col>
  </el-row>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "IntegralRecord",
    data() {
      return {
        loading: false,//
        filters: {
          name: ''
        },
        items: [],
        total: 0,
        value1: '',//
        value2: '',//
        options: [
          {value: ''},
          {value: ''},
          {value: ''},
        ],
        publishVisible: false,
        publishForm: {},
        notarizeVisible: false,
        state: []
      }
    },
    methods: {
      dateTimeFormat(value) {
        let time = new Date(+value);
        let rightTwo = (v) => {
          v = '0' + v;
          return v.substring(v.length - 2, v.length)
        };
        if (time == null) return;
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let date = time.getDate();
        let hours = time.getHours();
        let minutes = time.getMinutes();
        let seconds = time.getSeconds();
        return year + '-' + rightTwo(month) + '-' + rightTwo(date) + ' ' + rightTwo(hours) + ':' + rightTwo(minutes) + ':' + rightTwo(seconds);
      },
      trim(str) {
        if(str != null){
          return str.replace(/(^\s+)|(\s+$)/g, "");
        }
      },
      handleCurrentChange(val) {
        this.page = val;
        this.seek(this.page);
      },
      //
      seek() {
        if (this.filters.startTime) {
          let date = new Date(this.filters.startTime);
          this.state.startTime = date.getTime();
        }
        if (this.filters.endTime) {
          let date = new Date(this.filters.endTime);
          this.state.endTime = date.getTime();
        }
        if (this.filters.startTime &&
          this.filters.endTime &&
          this.state.endTime - this.state.startTime <= 0) {
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          });
          return;
        }
        axios({
          method: 'post',
          url: '/api/agent/queryscorerecord',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'size': '15',//
            'page': this.page,//
            'agentId': this.trim(this.filters.id),
            'agent': this.trim(this.filters.agent),
            'startTime': this.state.startTime, /**/
            'endTime': this.state.endTime
          }
        })
          .then((res) => {
              this.loading = false;//
              this.items = res.data.data.items;
              this.total = res.data.data.pageCount;
              //console.log(res.data.data.items)
              for (let i = 0; i < this.items.length; i++) {
                this.items[i].accountingTime = this.dateTimeFormat(this.items[i].accountingTime);
              }
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      },
      selsChange: function (sels) {
        this.sels = sels;
      },
      publishDialog: function (index, row) {
        this.publishVisible = true;
        this.publishForm = Object.assign({}, row);
      },

      //
      sure() {
        axios({
          method: 'post',
          url: '/api/agent/scoremanager',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'agentId': this.trim(this.publishForm.agentId),
            'card': this.trim(this.publishForm.product),
            'cardAmount': this.trim(this.publishForm.afternumber),
            'scoreAmount': this.trim(this.publishForm.afterAmount)
          }
        })
          .then((res) => {
              //console.log(res.data.success)
              if (res.data.success == false) {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
              } else if (res.data.success == true) {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                });
                this.notarizeVisible = false;
                this.seek();
              }
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      }
    },
    mounted() {
      this.seek();
    }
  }
</script>

<style scoped>
  .warp-main {
    margin-top: 30px;
  }

  .memberInput {
    width: 220px;
  }
</style>



