<!---->
<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.role" placeholder="" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" v-on:click="handleSearch"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!-- -->
      <el-table :data="role" highlight-current-row @selection-change="selsChange"
                style="width: 100%;">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column type="index" width="60"></el-table-column>
        <el-table-column prop="role" label="" width="100" sortable></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.$index,scope.row)"></el-button>
            <el-button size="small" @click="showchoose(scope.$index,scope.row)"></el-button>
            <el-button type="danger" @click="delBook(scope.$index,scope.row)" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-col :span="24" class="toolbar">
        <el-button type="danger" @click="batchDeleteBook" :disabled="this.sels.length===0"></el-button>
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total"
                       style="float:right;">
        </el-pagination>
      </el-col>

      <!---->
      <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
          <el-form-item label="" prop="role">
            <el-input v-model="editForm.role" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false"></el-button>
          <el-button type="primary" @click.native="editSubmit"></el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
          <el-form-item label="" prop="role">
            <el-input v-model="addForm.role" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false"></el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="chooseFormVisible" :close-on-click-modal="false">
        <el-form :model="chooseForm" label-width="100px">
          <el-table :data="privilege" highlight-current-row @selection-change="selsChange"
                    style="width: 100%;">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column type="index" width="60"></el-table-column>
            <el-table-column prop="privilege" label=""></el-table-column>
          </el-table>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="chooseFormVisible = false"></el-button>
          <el-button type="danger" @click.native="chooseSubmit" :disabled="this.sels.length===0"></el-button>
        </div>
      </el-dialog>

    </el-col>
  </el-row>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "Admin",
    data() {
      return {
        role: [],//
        privilege: [],//
        filters: {
          name: ''
        },
        total: 0,
        page: 1,
        limit: 10,
        loading: false,
        sels: [], //

        //
        editFormVisible: false,//
        chooseFormVisible: false,//
        editFormRules: {
          role: [
            {required: true, message: '', trigger: 'blur'}
          ],
        },
        editForm: {
          id: 0,
          name: '',
          author: '',
          publishAt: '',
          description: ''
        },
        chooseForm: {
          id: 0,
          name: '',
          author: '',
          publishAt: '',
          description: ''
        },
        //
        addFormVisible: false,//
        addLoading: false,
        addFormRules: {
          role: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        addForm: {
          name: '',
          author: '',
          publishAt: '',
          description: ''
        }
      }
    },
    methods: {
      trim(str) {
        if(str != null){
          return str.replace(/(^\s+)|(\s+$)/g, "");
        }
      },
      handleCurrentChange(val) {
        this.page = val;
        this.handleSearch(this.page);
      },
      handleSearch() {
        axios({//
          method: 'post',
          url: '/api/role/queryrole',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'page': this.page,
            'size': '15',
            'role': this.trim(this.filters.role)
          }
        })
          .then((res) => {
              //console.log(res)
              this.role = res.data.data.items;
              this.total = res.data.data.pageCount;
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      },
      selsChange: function (sels) {
        this.sels = sels;
      },
      //
      delBook: function (index, row) {
        let that = this;
        this.$confirm('?', '', {type: 'warning'}).then(() => {
          that.loading = true;
          axios({
            method: 'post',
            url: '/api/role/deleterole',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'id': row.id
            }
          })
            .then((res) => {
                that.loading = false;
                if (res.data.success == true) {
                  that.$message.success({showClose: true, message: '', duration: 1500});
                  that.handleSearch();
                } else {
                  that.$message.error({showClose: true, message: err.toString(), duration: 2000});
                }
              },
            ).catch((e) => {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '', duration: 2000});
          });
        });
      },
      //
      showEditDialog: function (index, row) {
        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
      },
      //
      editSubmit: function () {
        axios({
          method: 'post',
          url: '/api/role/updaterole',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'id': this.trim(this.editForm.id),
            'role': this.trim(this.editForm.role)
          }
        })
          .then((res) => {
              if (res.data.success == false) {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
              } else if (res.data.success == true) {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                });
                this.editFormVisible = false;//
                this.handleSearch();
              }
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      },
      showAddDialog: function () {
        this.addFormVisible = true;
        this.addForm = {
          name: '',
          author: '',
          publishAt: '',
          description: ''
        };
      },

      //
      showchoose: function (index, row) {
        //
        axios({
          method: 'post',
          url: '/api/privilege/queryallprivilege',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
        })
          .then((res) => {
              this.privilege = res.data.data;
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });

        this.chooseFormVisible = true;
        this.editForm = Object.assign({}, row);
      },
      //
      chooseSubmit: function () {
        let ids = this.sels.map(item => item.id).toString();
        let that = this;
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          that.loading = true;
          axios({
            method: 'post',
            url: '/api/role/editprivilege',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'roleId': this.trim(this.editForm.id),//id
              'privilegeId': ids//id
            }
          })
            .then((res) => {
                that.loading = false;
                if (res.data.success == true) {
                  that.$message.success({showClose: true, message: '', duration: 1500});
                  this.chooseFormVisible = false;//
                } else {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.chooseFormVisible = false;//
                }
              },
            ).catch((e) => {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '', duration: 2000});
          });
        });
      },
      //
      addSubmit: function () {
        axios({
          method: 'post',
          url: '/api/role/addrole',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'role': this.trim(this.addForm.role)//
          }
        })
          .then((res) => {
              if (res.data.success == false) {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
              } else if (res.data.success == true) {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                });
                this.addFormVisible = false;//
                this.handleSearch();
              }
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      },
      //
      batchDeleteBook: function () {
        let ids = this.sels.map(item => item.id).toString();
        let that = this;
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          that.loading = true;
          axios({
            method: 'post',
            url: '/api/role/deleterole',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'id': ids
            }
          })
            .then((res) => {
                that.loading = false;
                if (res.data.success == true) {
                  that.$message.success({showClose: true, message: '', duration: 1500});
                  that.handleSearch(1);
                } else {
                  that.$message.error({showClose: true, message: err.toString(), duration: 2000});
                }
              },
            ).catch((e) => {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '', duration: 2000});
          });

        });
      }
    },
    mounted() {
      this.handleSearch()
    }
  }
</script>

<style>
  .demo-table-expand label {
    font-weight: bold;
  }

  .toolbar {
    margin-top: 30px;
  }
</style>



