<!---->
<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.adminname" placeholder="" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" v-on:click="handleSearch"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!-- -->
      <el-table :data="list" highlight-current-row @selection-change="selsChange"
                style="width: 100%;">
        <el-table-column type="index" width="60"></el-table-column>
        <el-table-column prop="adminname" label="" width="160" sortable></el-table-column>
        <el-table-column prop="title" label="" width="180" sortable></el-table-column>
        <el-table-column prop="file" label="" width="100" sortable>
          <template slot-scope="scope">
            <img :src="scope.row.file" alt="" style="width: 50px;height: 50px">
          </template>
        </el-table-column>
        <el-table-column prop="number" label="" width="140" sortable></el-table-column>
        <el-table-column prop="integral" label="" width="140" sortable></el-table-column>
        <el-table-column prop="vipcard" label="()"></el-table-column>
      </el-table>

      <!---->
      <el-col :span="24" class="toolbar">
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total"
                       style="float:right;">
        </el-pagination>
      </el-col>

      <!---->
      <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
        <el-form :model="normalForm" label-width="150px" :rules="rules" class="demo-ruleForm">
          <el-form-item label="" prop="title">
            <el-input v-model="normalForm.title" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="file">
            <div class="upload">
              <el-upload
                class="avatar-uploader"
                :action=domain
                :http-request=upqiniu
                :show-file-list="false"
                :before-upload="beforeUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <el-button v-else class="el-icon-plus avatar-uploader-icon"></el-button>
              </el-upload>
            </div>
          </el-form-item>
          <el-form-item label="" prop="compensation">
            <el-select v-model="value" placeholder="" @change="change">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <el-form-item label="" prop="number" class="memberCard" v-show="memberDisplay">
              <el-input type="number" min="0" class="memberInput" v-model="normalForm.number"
                        placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="integral" class="memberCard" v-show="memberDisplay">
              <el-input type="number" min="0" class="memberInput" v-model="normalForm.integral"
                        placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="vipcard" class="memberCard" v-show="memberDisplay">
              <el-input type="number" min="0" class="memberInput" v-model="normalForm.vipcard"
                        placeholder=""></el-input>&nbsp;
            </el-form-item>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="issue"></el-button>
            <el-button type="primary" @click.native="addFormVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "SystemRecovery",
    data() {
      return {
        imageUrl: '',
        // 
        domain: 'http://up.qiniu.com',
        // 
        qiniuaddr: 'qiniu.3cscy.com',
        memberDisplay: false,
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        value: '',
        normalForm: {},
        rules: {
          title: [
            {required: true, message: '', trigger: 'blur'}
          ],
          file: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        list: [],
        filters: {
          name: ''
        },
        total: 0,
        loading: false,
        addFormVisible: false,//
      }
    },
    methods: {
      trim(str) {
        if(str != null){
          return str.replace(/(^\s+)|(\s+$)/g, "");
        }
      },
      // 
      upqiniu(req) {
        //console.log(req)
        const config = {
          headers: {'Content-Type': 'multipart/form-data'}
        }
        let filetype = ''
        if (req.file.type === 'image/png') {
          filetype = 'png'
        } else {
          filetype = 'jpg'
        }
        // 
        const keyname = 'anbang' + Math.random() + '.' + filetype
        // token
        axios({
          method: 'post',
          url: '/api/mailctrl/uptoken',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
        }).then(res => {
          const formdata = new FormData()
          formdata.append('file', req.file)
          formdata.append('token', res.data.data)
          formdata.append('key', keyname)
          // 
          axios.post(this.domain, formdata, config).then(res => {
            this.imageUrl = 'http://' + this.qiniuaddr + '/' + res.data.key
            //console.log(this.imageUrl)
          })
        })
      },
      // 
      beforeUpload(file) {
        const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
        const isLt2M = file.size / 1024 / 1024 < 2
        if (!isJPG) {
          this.$message.error(' JPG !')
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!')
        }
        return isJPG && isLt2M
      },

      change() {
        if (this.value == '1') {
          this.memberDisplay = false;
        } else if (this.value == '2') {
          this.memberDisplay = true;
        }
      },
      //
      issue() {
        if (this.normalForm.title == undefined || this.normalForm.title == "") {
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          });
        } else if (this.normalForm.number < 0 || this.normalForm.integral < 0 || this.normalForm.vipcard < 0) {
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          });
        } else {
          axios({
            method: 'post',
            url: '/api/mailctrl/addmail',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'status': 1,
              'title': this.trim(this.normalForm.title),
              'file': this.imageUrl,
              'number': this.trim(this.normalForm.number),
              'integral': this.trim(this.normalForm.integral),
              'vipcard': this.trim(this.normalForm.vipcard)
            }
          })
            .then((res) => {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                });
                this.normalForm.title = '';
                this.normalForm.file = '';
                this.normalForm.number = '';
                this.normalForm.integral = '';
                this.normalForm.vipcard = '';
                this.addFormVisible = false;//
                this.handleSearch();
              },
            ).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break;
                case 500:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break;
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break;
                case 400:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break;
              }
            }
          });
        }
      },
      showAddDialog: function () {
        this.addFormVisible = true;
        this.addForm = {
          name: '',
          author: '',
          publishAt: '',
          description: ''
        };
      },
      handleCurrentChange(val) {
        this.page = val;
        this.handleSearch(this.page);
      },
      handleSearch() {
        this.loading = true;//
        axios({//
          method: 'post',
          url: '/api/mailctrl/querymail',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'status': 1,
            'size': '15',//
            'page': this.page,//
            'adminname': this.trim(this.filters.adminname)
          }
        })
          .then((res) => {
              this.loading = false;//
              this.list = res.data.list;
              this.total = res.data.count;//
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
      },
      selsChange: function (sels) {
        this.sels = sels;
      },
    },
    mounted() { //
      this.handleSearch()
    }
  }
</script>

<style scoped>
  .memberCard {
    margin-top: 20px;
  }

  .memberInput {
    width: 50%;
  }

  .warp-main {
    margin-top: 20px;
  }

  img {
    width: 180px;
    height: 120px;
  }
</style>



