<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>
    <el-select v-model="value8" filterable placeholder="" class="choice" @change="change(value8)">
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <!---->
    <el-form :model="normalForm" :rules="rules" ref="normalForm" label-width="250px" class="demo-ruleForm" v-show="normalshow">
      <el-form-item label="" prop="goldForNewNember ">
        <el-input v-model="normalForm.goldForNewNember "></el-input>
      </el-form-item>
      <el-form-item label="" prop="signGoldNumber ">
        <el-input v-model="normalForm.signGoldNumber "></el-input>
      </el-form-item>
      <el-form-item label="" prop="inviteIntegralNumber">
        <el-input v-model="normalForm.inviteIntegralNumber"></el-input>
      </el-form-item>
      <el-form-item label="" prop="planGrowIntegralSpeed">
        <el-input v-model="normalForm.planGrowIntegralSpeed"></el-input>
      </el-form-item>
      <el-form-item label="" prop="planMemberCreateRoomDailyGoldPrice">
        <el-input v-model="normalForm.planMemberCreateRoomDailyGoldPrice"></el-input>
      </el-form-item>
      <el-form-item label="" prop="planMemberaddRoomDailyGoldPrice">
        <el-input v-model="normalForm.planMemberaddRoomDailyGoldPrice"></el-input>
      </el-form-item>
      <el-form-item label="" prop="planMemberMaxCreateRoomDaily">
        <el-input v-model="normalForm.planMemberMaxCreateRoomDaily"></el-input>
      </el-form-item>
      <el-form-item label="" prop="planMemberRoomsCount">
        <el-input v-model="normalForm.planMemberRoomsCount"></el-input>
      </el-form-item>
      <el-form-item label="" prop="planMemberRoomsAliveHours">
        <el-input v-model="normalForm.planMemberRoomsAliveHours"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('normalForm')"></el-button>
        <el-button @click="change('')"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-form :model="memberForm" :rules="rules" ref="memberForm" label-width="150px" class="demo-ruleForm" v-show="membershow">
      <el-form-item label="" prop="signGoldNumber ">
        <el-input v-model="memberForm.signGoldNumber "></el-input>
      </el-form-item>
      <el-form-item label="" prop="inviteIntegralNumber">
        <el-input v-model="memberForm.inviteIntegralNumber"></el-input>
      </el-form-item>
      <el-form-item label="" prop="vipGrowIntegralSpeed">
        <el-input v-model="memberForm.vipGrowIntegralSpeed"></el-input>
      </el-form-item>
      <el-form-item label="" prop="vipGrowGradeSpeed">
        <el-input v-model="memberForm.vipGrowGradeSpeed"></el-input>
      </el-form-item>
      <el-form-item label="" prop="vipMemberRoomsCount">
        <el-input v-model="memberForm.vipMemberRoomsCount"></el-input>
      </el-form-item>
      <el-form-item label="" prop="vipMemberRoomsAliveHours">
        <el-input v-model="memberForm.vipMemberRoomsAliveHours"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitmemberForm()"></el-button>
        <el-button @click="change('')"></el-button>
      </el-form-item>
    </el-form>
   </el-row>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "MemberRules",
      data() {
        return {
          normalshow:false,
          membershow:false,
          memberDisplay:false,
          options: [{
            value: '',
            label: ''
          }, {
            value: '',
            label: ''
          }],
          card: [{
            value:'1',
            label:''
          },{
            value:'2',
            label:''
          },{
            value:'3',
            label:''
          }],
          value8: '',
          value:'',
          normalForm: {},
          memberForm: {},
          rules: {
            newnumber: [
              {required: true, message: '', trigger: 'blur'}
            ],
            number1: [
              { required: true, message: '', trigger: 'blur' }
            ],
            number4: [
              { required: true, message: '', trigger: 'blur' }
            ],
            speed: [
              { required: true, message: '', trigger: 'blur' }
            ],
            createprice: [
              { required: true, message: '', trigger: 'blur' }
            ],
            price: [
              { required: true, message: '', trigger: 'blur' }
            ],
            rooms: [
              { required: true, message: '', trigger: 'blur' }
            ],
            number: [
              { required: true, message: '', trigger: 'blur' }
            ],
            hours: [
              { required: true, message: '', trigger: 'blur' }
            ],
            memberCard: [
              { required: true, message: '', trigger: 'blur' }
            ],
            money: [
              { required: true, message: '', trigger: 'blur' }
            ],
            integral: [
              { required: true, message: '', trigger: 'blur' }
            ],
            gold: [
              { required: true, message: '', trigger: 'blur' }
            ]
          },
        }
      },
      methods: {
        trim(str) {
          if(str != null){
            return str.replace(/(^\s+)|(\s+$)/g, "");
          }
        },
        change(value8){
          if(this.value8 == ''){
            this.normalshow = true;
            this.membershow = false;
            axios({
              method: 'post',
              url: '/api/rights/commonrights',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params:{}
            })
              .then((res) => {
                this.normalForm = res.data;
            })
          }else{
            this.normalshow = false;
            this.membershow = true;
            axios({
              method: 'post',
              url: '/api/rights/viprights',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params:{}
            })
              .then((res) => {
                this.memberForm = res.data;
              })
          }
        },
        //
        submitForm:function(){
          axios({
            method: 'post',
            url: '/api/rights/commonuser',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params:{
              signGoldNumber:this.normalForm.signGoldNumber,
              shareIntegralNumber:this.normalForm.shareIntegralNumber,
              goldForNewNember:this.normalForm.goldForNewNember,
              shareGoldNumber:this.normalForm.shareGoldNumber,
              inviteIntegralNumber:this.normalForm.inviteIntegralNumber,
              planGrowIntegralSpeed:this.normalForm.planGrowIntegralSpeed,
              planMemberCreateRoomDailyGoldPrice:this.normalForm.planMemberCreateRoomDailyGoldPrice,
              planMemberaddRoomDailyGoldPrice:this.normalForm.planMemberaddRoomDailyGoldPrice,
              planMemberRoomsCount:this.normalForm.planMemberRoomsCount,
              planMemberMaxCreateRoomDaily:this.normalForm.planMemberMaxCreateRoomDaily,
              planMemberRoomsAliveHours:this.normalForm.planMemberRoomsAliveHours
            }
          })
            .then((res) => {
              console.log(res)
                if (res.data == "fail") {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                } else if (res.data == "success") {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                  });
                  this.change('');
                }
              },
            ).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
              }
            }
          })
        },
        //
        submitmemberForm:function(){
          console.log(this.memberForm.vipMemberRoomsCount)
          axios({
            method: 'post',
            url: '/api/rights/vipuser',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params:{
              signGoldNumber:this.memberForm.signGoldNumber,
              shareIntegralNumber:this.memberForm.shareIntegralNumber,
              shareGoldNumber:this.memberForm.shareGoldNumber,
              inviteIntegralNumber:this.memberForm.inviteIntegralNumber,
              vipGrowIntegralSpeed:this.memberForm.vipGrowIntegralSpeed,
              vipGrowGradeSpeed:this.memberForm.vipGrowGradeSpeed,
              vipMemberRoomsCount:this.memberForm.vipMemberRoomsCount,
              vipMemberRoomsAliveHours:this.memberForm.vipMemberRoomsAliveHours
            }
          })
            .then((res) => {
                console.log(res)
                if (res.data == "fail") {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                } else if (res.data == "success") {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                  });
                  this.change('');
                }
              },
            ).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
              }
            }
          })
        }
      }
    }
</script>

<style scoped>
  .choice {
    margin-top:20px;
  }
  .demo-ruleForm {
    margin-top:50px;
  }
  .memberCard {
    margin-top:20px;
  }
  .memberInput {
    width:40%;
  }
  .el-input{
    width:500px;
  }
</style>



