<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item label="ID">
          <el-input v-model="filters.nickname" @keyup.enter.native="handleSearch"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="filters.nickname" @keyup.enter.native="handleSearch"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="filters.nickname" @keyup.enter.native="handleSearch"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="filters.nickname" @keyup.enter.native="handleSearch"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="68px">
          <el-date-picker
            v-model="filters.startTime"
            type="date"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" label-width="68px" style="margin-left:-44px;">
          <el-date-picker
            v-model="filters.endTime"
            type="date"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" label-width="83px">
          <el-select v-model="filters.mailType" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="handleSearch"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!-- -->
    <el-table :data="record" highlight-current-row style="width: 100%;">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column type="index" width="60"></el-table-column>
      <el-table-column prop="id" label="ID" width="auto" sortable></el-table-column>
      <el-table-column prop="nickname" label="" width="auto" sortable></el-table-column>
      <el-table-column prop="level" label="" width="auto" sortable></el-table-column>
      <el-table-column prop="createTime" label="" width="auto" sortable></el-table-column>
      <el-table-column prop="bossId" label="ID" width="auto" sortable></el-table-column>
      <el-table-column prop="bossName" label="" width="auto" sortable></el-table-column>
      <el-table-column prop="phone" label="" width="auto" sortable></el-table-column>
      <el-table-column prop="userName" label="" width="auto" sortable></el-table-column>
      <el-table-column prop="systemMail.createtime" label="" width="auto" sortable>
        <template slot-scope="scope">
          <el-button type="text" @click="particulars(scope.$index,scope.row)"></el-button>
          <el-button type="text" @click="operation(scope.$index,scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="centerDialogVisible" :close-on-click-modal="false">
      <el-table :data="roles" style="width: 100%;">
        <el-table-column label="">
          <template slot-scope="scope">
            <img :src="scope.row.headimgurl" alt="" class="pic">
          </template>
        </el-table-column>
        <el-table-column label="" prop="nickname" sortable></el-table-column>
        <el-table-column label="ID" prop="id" sortable></el-table-column>
        <el-table-column label="" prop="gender" sortable></el-table-column>
        <el-table-column label="" prop="state" sortable></el-table-column>
        <el-table-column label="" prop="createTime" sortable></el-table-column>
      </el-table>
      <el-table :data="roles" style="width: 100%;">
          <el-table-column label="" prop="level" sortable></el-table-column>
          <el-table-column label="" prop="clubCardZhou" sortable></el-table-column>
          <el-table-column label="" prop="clubCardYue" sortable></el-table-column>
          <el-table-column label="" prop="clubCardJi" sortable></el-table-column>
          <el-table-column label="" prop="cost" sortable></el-table-column>
          <el-table-column label="" prop="invitationCode" sortable></el-table-column>
        </el-table>
      <div slot="footer" class="dialog-footer">
        <router-link :to="{path:'/membershipCardPurchaseRecord'}">
          <el-button></el-button>
        </router-link>
        <router-link :to="{path:'/membershipCardConsumptionRecord'}">
          <el-button></el-button>
        </router-link>
        <el-button @click.native="CancelSubmit" v-if="disqualifyDialogVisible"></el-button>
        <el-button @click.native="relieveSubmit" v-if="unlockingDialogVisible"></el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="operateVisible" :close-on-click-modal="false">
      <div class="left">
        <el-button class="bound" type="primary" @click="pinless()"></el-button><br>
        <el-button class="levelup" @click="levelup()"></el-button>
      </div>
      <el-form ref="form" :model="form" label-width="100px" class="form" v-if="boundVisible">
        <el-form-item label="ID">
          <el-input v-model="form.bossId1"></el-input>
        </el-form-item>
        <el-form-item label="ID">
          <el-input v-model="form.id"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="bound()"></el-button>
          <el-button @click="unbound()"></el-button>
        </el-form-item>
      </el-form>
      <el-form ref="form" :model="form" label-width="100px" class="form" v-if="LevelUpVisible">
        <el-form-item label="ID">
          <el-input v-model="form.id"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.level"></el-input>
        </el-form-item>
        <el-form-item label="">
          <!--<el-input v-model="form.upgrade"></el-input>-->
          <el-select v-model="form.upgrade" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="makeSure()"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!---->
    <el-col :span="24" class="toolbar">
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total"
                     style="float:right;">
      </el-pagination>
    </el-col>
  </el-row>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "PromoterManagement",
        data(){
          return{
            filters:{},
            record:[],
            form:{},
            roles:[],
            total:0,
            upgrade:'',
            options: [
              {value: ''},
              {value: ''}
            ],
            centerDialogVisible:false,
            relieveDialogVisible:false,
            LevelUpVisible:false,
            boundVisible:false,
            operateVisible:false,
            disqualifyDialogVisible:true,
            unlockingDialogVisible:false
          }
        },
      methods:{
        dateTimeFormat(value) {
          let time = new Date(+value);
          let rightTwo = (v) => {
            v = '0' + v;
            return v.substring(v.length - 2, v.length)
          };
          if (time == null) return;
          let year = time.getFullYear();
          let month = time.getMonth() + 1;
          let date = time.getDate();
          let hours = time.getHours();
          let minutes = time.getMinutes();
          let seconds = time.getSeconds();
          return year + '-' + rightTwo(month) + '-' + rightTwo(date) + ' ' + rightTwo(hours) + ':' + rightTwo(minutes) + ':' + rightTwo(seconds);
        },
        handleSearch(page){
          axios({
            url:'/api/agent/queryagent',
            method:'post',
            params:{
              nickname:this.filters.mailType,
              page:page,
              size:10
            }
          }).then((res) => {
              console.log(res.data)
              this.record = res.data.data.items;
              this.total = res.data.data.pageCount;
              for(let i = 0;i < this.record.length;i++){
                this.record[i].createTime = this.dateTimeFormat(this.record[i].createTime)
                if(this.record[i].level == 1){
                  this.record[i].level = '';
                  this.record[i].bossId1 = this.record[i].id;
                }
                if(this.record[i].level == 2){
                  this.record[i].level = '';
                  this.record[i].bossId1 = this.record[i].bossId;
                }
              }
            }
          ).catch((e) => {
            this.$message.error({showClose: true, message: '', duration: 2000});
          })
        },
        //
        particulars:function(index,row){
          this.centerDialogVisible = true;
          // console.log(row)
          axios({
            url:'/api/agent/agentdetail',
            method:'post',
            params:{
              agentId:row.id
            }
          }).then((res) => {
              row.clubCardYue = res.data.data.clubCardYue;
              row.clubCardJi = res.data.data.clubCardJi;
              row.clubCardZhou = res.data.data.clubCardZhou;
              row.score = res.data.data.score;
              this.roles = [Object.assign({}, row)];
          }).catch((e) => {
              this.$message.error({showClose: true, message: '', duration: 2000});
          })
        },
        //
        operation:function(index,row){
          this.operateVisible = true;
          this.boundVisible = true;
          this.LevelUpVisible = false;
          this.form = Object.assign({}, row);
        },
        //
        bound:function(){
          axios({
            url:'/api/agent/setboss',
            method:'post',
            params:{
              agentId:this.form.id,
              bossId:this.form.bossId
            }
          }).then((res) => {
            console.log(res.data)
            if(res.data.success){
              this.$message.success({showClose: true, message: '', duration: 1500});
              this.operateVisible = false;
              this.handleSearch(1)
            }else{
              this.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }
          }).catch((e) => {
            this.$message.error({showClose: true, message: '', duration: 2000});
          })
        },
        //
        unbound:function(){
          axios({
            url:'/api/agent/removeboss',
            method:'post',
            params:{
              agentId:this.form.id
            }
          }).then((res) => {
            console.log(res.data)
            if(res.data.success){
              this.$message.success({showClose: true, message: '', duration: 1500});
              this.operateVisible = false;
              this.handleSearch(1)
            }else{
              this.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }
          }).catch((e) => {
            this.$message.error({showClose: true, message: '', duration: 2000});
          })
        },
        //
        pinless:function(){
          this.LevelUpVisible = false;
          this.boundVisible = true;
        },
        //
        levelup:function(){
          this.LevelUpVisible = true;
          this.boundVisible = false;
        },
        //
        makeSure:function(){
          if(this.form.upgrade == ''){
            this.upgrade = 1;
          }
          if(this.form.upgrade == ''){
            this.upgrade = 2;
          }
          axios({
            url:'/api/agent/setlevel',
            method:'post',
            params:{
              agentId:this.form.id,
              level:this.upgrade
            }
          }).then((res) => {
            console.log(res.data)
            if(res.data.success){
              this.$message.success({showClose: true, message: '', duration: 1500});
              this.operateVisible = false;
              // this.relieveDialogVisible = false;
              this.handleSearch(1)
            }else{
              this.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }
          }).catch((e) => {
            this.$message.error({showClose: true, message: '', duration: 2000});
          })
        },
        CancelSubmit:function(){
          axios({
            url:'/api/agent/ban',
            method:'post',
            params:{
              agentId:this.roles[0].id
            }
          }).then((res) => {
            console.log(res.data)
            if(res.data.success){
              this.unlockingDialogVisible = true;
              this.disqualifyDialogVisible = false;
              this.roles[0].state = res.data.data
              this.handleSearch(1)
            }else{
              this.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }
          }).catch((e) => {
            this.$message.error({showClose: true, message: '', duration: 2000});
          })
        },
        relieveSubmit:function(){
          axios({
            url:'/api/agent/liberate',
            method:'post',
            params:{
              agentId:this.roles[0].id
            }
          }).then((res) => {
            console.log(res.data)
            if(res.data.success){
              this.unlockingDialogVisible = false;
              this.disqualifyDialogVisible = true;
              this.roles[0].state = res.data.data
              this.handleSearch(1)
            }else{
              this.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }
          }).catch((e) => {
            this.$message.error({showClose: true, message: '', duration: 2000});
          })
        },
        handleCurrentChange:function(){}
      },
      mounted(){
          this.handleSearch(1);
      }
    }
</script>

<style scoped>
  .toolbar{
    margin-top:30px;
  }
  .img{
    float:left;
    margin-right:60px;
  }
  .left,
  .form{
    display:inline-block;
  }
  .left{
    position: absolute;
    top:60px;
  }
  .form{
    margin-left:140px;
  }
  .bound,
  .levelup{
    width:100px;
  }
  .levelup{
    margin-top:20px;
  }
  a{
    text-decoration: none;
    color:#fff;
  }
  .pic{
    width:50px;
    height:50px;
  }
</style>



